:root {
    /* colors */
    --primary-100: rgba(215, 189, 223, 0.5);
    --primary-200: rgb(206, 147, 216);
    --primary-300: #ab47bc;
    --primary-400: #8e24aa;
    --primary-500: rgb(213, 149, 241);
    --primary-600: #6a1b9a;
    --primary-700: #4a148c;
  
    
    /* grey */
    --grey-50: rgb(248, 250, 252);
    --grey-100: #f1f5f9;
    --grey-200: #e2e8f0;
    --grey-300: rgb(178, 180, 183);
    --grey-400: #d4deed;
    --grey-500: #64748b;
    --grey-600: #475569;
    --grey-700: #334155;
    --grey-800: #1e293b;
    --grey-900: #0f172a;
  
    /* rest of the colors */
    --black: #222;
    --white: #fff;
    --red-light: #f8d7da;
    --red-dark: #de354c;
    --green-light: #d1e7dd;
    --green-dark: #03a032;
  
    /* fonts  */
    --headingFont: "Lato", sans-serif;
    --bodyFont: "Lato", sans-serif;
    --smallText: 0.7em;
    /* rest of the vars */
    --backgroundColor: var(--grey-100);
    --textColor: var(--grey-900);
    --borderRadius: 0.25rem;
    --letterSpacing: 1px;
    --transition: 0.3s ease-in-out all;
    --max-width: 1120px;
    --fixed-width: 600px;
    --nav-width: 200px;
  
    /* box shadow*/
    --shadow-1: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    --shadow-2: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    --shadow-3: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    --shadow-4: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    --shadow-5: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
    --shadow-6: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset,
      rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
      rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  }


.img {
    width: 100%;
    height: 10rem;
}
.logo{
